@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color: #2f3640;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  color: #fff;
}


/* Navbar */

nav {
  position: relative;
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #fff;
}

nav .logo {
  width: 20%;
}

nav .logo a img {
  width: 20%;
}

nav .search-bar {
  width: 30%;
}


nav .logo a {
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  font-weight: 400;
  color: #fff;
  text-decoration: none;
}

nav .search-bar {
  position: relative;
  display: flex;
  align-items: center;
}

nav .search-bar input {
  background-color: transparent;
  padding: .5rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.479);
  width: 500px;
  height: 40px;
  border-radius: 30px;
  outline: none;
  color: #fff;
  text-transform: capitalize;
}

nav .search-bar svg {
  position: absolute;
  top: 10%;
  right: 13%;
  color: rgba(255, 255, 255, 0.479);
  font-size: 1.8rem;
  margin: 0 1rem;
  cursor: pointer;
}

nav .links ul {
  list-style-type: none;
  display: flex;
}

nav .links a {
  margin: 0 1rem;
  text-decoration: none;
  color: #fff;
}

/* Navbar */

/* Hero */

.hero {
  position: relative;
  width: 100%;
  height: 50vh;
  margin: 4rem 0;
}

.hero-img {
  position: relative;
  width: 85%;
  height: 100%;
  margin: 0 auto;
  background-image: url('https://i1.wp.com/redanianintelligence.com/wp-content/uploads/2020/09/ger1.jpg?fit=1280%2C720');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 30px;
}

/* Content */

.content {
  position: relative;
  width: 100%;
  color: #fff;
  display: flex;
  flex-direction: column;
  padding: 5rem 10%;
}

.genre {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Films */

.films {
  position: relative;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 7rem;
}

.films .film {
  position: relative;
  width: 340px;
  overflow: hidden;
  height: 600px;
  margin: -2rem 1rem;
}

.films .film .film-img {
  position: relative;
  width: 100%;
  height: 430px;
}

.films .film .film-img img {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 20px;
}

.films .film .film-img::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2f364050;
}

.films .film h1 {
  font-weight: 400;
  font-size: 1.4rem;
  margin-top: 1rem;
}

.films .film span {
  position: absolute;
  top: 3%;
  left: 3%;
  background-color: #3498db;
  padding: .2rem .5rem;
}

/* Pagination */

.pagi {
  position: relative;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Slide */

.slide {
  position: relative;
  width: 100%;
}

.slide .card {
  width: 20%;
}

.films .cover-title {
  width: 100%;
  height: 20vh;
  color: #fff;
  font-size: 2rem;
  text-align: center;
  margin-top: -5rem;
}

.search-title {
  color: #fff;
  margin-top: 4rem;
  margin-left: 11%;
}

/* Error */

.error {
  position: relative;
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error h1 {
  color: #fff;
  font-size: 3rem;
}

.error p {
  color: #fff;
  font-size: 1.2rem;
  line-height: 2.5;
}

.error a {
  text-decoration: none;
}

/* Movie */

.movie {
  position: relative;
  width: 100%;
  display: flex;
  padding: 0 10%;
  margin-top: 5rem;
}

.movie .movie-image,
.movie .movie-content {
  width: 50%;
}

.movie .movie-image {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.movie .movie-image img {
  width: 80%;
  height: 80%;
  object-fit: fill;
}

.movie .movie-content {
  padding: 3rem 0;
}

.movie .movie-content h1 {
  color: #fff;
}

.movie .movie-content p {
  color: #fff;
  line-height: 1.8;
}

.movie .movie-content .info {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0;
}

.movie .movie-content .info span {
  color: #fff;
  text-align: center;
  display: block;
}

.movie .genres {
  display: flex;
  margin: 2rem 0;
}

.movie .genres span {
  color: #fff;
  padding: .3rem 1.5rem;
  background-color: #34495e;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 1.5rem;
}

.movie button {
  margin: 3rem 1rem;
}


/* Splide */

.similar {
  position: relative;
  width: 100%;
  padding: 0 10%;
}

.similar h1 {
  color: #fff;
  margin-bottom: 5rem;
}

.similar .splide {
  padding: 0 5%;
}

.splide .film {
  width: 80%;
}

.splide .film .film-img {
  position: relative;
  width: 100%;
  height: 500px;
}

.splide .film .film-img img {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 20px;
}

.splide .film .film-img::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2f364050;
}

.splide .film h1 {
  font-weight: 400;
  font-size: 1.4rem;
  margin-top: 1rem;
}

.splide .film span {
  position: absolute;
  top: 3%;
  left: 3%;
  background-color: #3498db;
  padding: .2rem .5rem;
}

/* Favorites */

.favorites {
  position: relative;
  width: 100%;
  margin: 4rem 0;
  display: flex;
  flex-wrap: wrap;
}

.favorites .fav {
  width: 40rem;
  height: 13rem;
  display: flex;
  margin: 2rem;
  border: 1px solid #1a1f25;
}

.fav .img {
  width: 40%;
  height: 100%;
  overflow: hidden;
}

.fav .img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fav-content {
  position: relative;
  padding: 1rem 2rem;
  width: 60%;
}

.fav-content h1 {
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 1rem;
}

.fav-content p {
  font-size: 13px;
  line-height: 1.7;
}

.fav-content button {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #e84118;
  color: #fff;
  border: none;
  padding: .2rem .8rem;
  font-size: 12px;
}